<template>
    <div class="css-select" style="max-width: 16rem;">
    <input type="hidden" name="select1" value="" data-css-select="hidden" />
    <input id="selected" type="text" class="css-select__selected" value="Выбрать клинику" aria-label="Select 1" readonly data-css-select="selected" />
    <div @click="selectClinicItem" class="css-select__dropdown">
        <button type="button" class="css-select__option" data-css-select="option1" onclick="cssSelect(this);">Инвитро</button>
        <button type="button" class="css-select__option" data-css-select="option2" onclick="cssSelect(this);">Семейный доктор</button>
        <button type="button" class="css-select__option" data-css-select="option3" onclick="cssSelect(this);">Новомед</button>
        <button type="button" class="css-select__option" data-css-select="option4" onclick="cssSelect(this);">Сити клиник</button>
        <button type="button" class="css-select__option" data-css-select="option5" onclick="cssSelect(this);">Доктор рядом</button>
    </div>
</div>
</template>

<script>

window.cssSelect = (option) => {
    const parent = option.parentNode.parentNode;
    parent.querySelector('[data-css-select="hidden"]').value = option.dataset.cssSelect;
    parent.querySelector('[data-css-select="selected"]').value = option.innerHTML;
    document.activeElement.blur();
};
document.addEventListener('mousedown', (event) => {
    const target = event.target;
    if (!(target.dataset.cssSelect && target.dataset.cssSelect === 'selected')) {
        return null;
    }
    if (window.getComputedStyle(target.nextElementSibling).visibility === 'visible') {
        setTimeout(() => void document.activeElement.blur(), 0);
    }
});

    export default {
        data() {
            return {

            }
        },
        methods: {
            selectClinicItem() {
                let clinic = document.getElementById('selected')
                console.log(clinic.value);
                clinic.style.backgroundColor = 'var( --hover-dark )'
                clinic.style.color = 'var( --main-white )'
                clinic.style.border = 'none'
                
            }
        },
    }
</script>

